<template>
  <v-container>
    <v-row>
      <v-col class="mb-4">
        <h1 class="display-2 font-weight-bold mb-3">
          Alta de Vehículos
        </h1>
      </v-col>
    </v-row>
    <v-row>
      <v-col
        cols="12"
        md="12"
      >
        <form @submit.prevent="guardarVehiculo()">
          <v-row>
            <v-col
              cols="12"
              md="4"
            >
              <v-select
                v-model="vehicles.brand"
                label="Marca"
                :items="marcas"
                outlined
                prepend-icon="mdi-tag"
                required
              />
            </v-col>

            <v-col
              cols="12"
              md="4"
            >
              <v-text-field
                v-model="vehicles.cod_tdp"
                label="COD TDP"
                outlined
                prepend-icon="mdi-barcode"
                required
              />
            </v-col>
            <v-col
              cols="12"
              md="4"
            >
              <v-text-field
                v-model="vehicles.serie_tdp"
                label="SERIE TDP"
                outlined
                prepend-icon="mdi-barcode-scan"
                required
              />
            </v-col>
          </v-row>

          <v-row>
            <v-col
              cols="12"
              md="4"
            >
              <v-select
                v-model="vehicles.category"
                label="Categoria"
                :items="categoria"
                outlined
                prepend-icon="mdi-group"
                required
              />
            </v-col>

            <v-col
              cols="12"
              md="8"
            >
              <v-text-field
                v-model="vehicles.model"
                label="Modelo"
                outlined
                prepend-icon="mdi-car"
                required
              />
            </v-col>
          </v-row>

          <v-row>
            <v-col
              cols="12"
              md="4"
            >
              <v-text-field
                v-model="vehicles.colour"
                label="Color"
                outlined
                prepend-icon="mdi-palette"
                required
              />
            </v-col>

            <v-col
              cols="12"
              md="4"
            >
              <v-text-field
                v-model="vehicles.manufacturing_year"
                label="Año Fabricación"
                outlined
                prepend-icon="mdi-calendar-range"
                type="number"
                required
              />
            </v-col>

            <v-col
              cols="12"
              md="4"
            >
              <v-text-field
                v-model="vehicles.model_year"
                label="Año Modelo"
                outlined
                prepend-icon="mdi-calendar-range"
                type="number"
                required
              />
            </v-col>
          </v-row>

          <v-row>
            <v-col
              cols="12"
              md="4"
            >
              <v-select
                v-model="vehicles.location"
                label="Ubicación"
                :items="opciones"
                outlined
                prepend-icon="mdi-crosshairs-gps"
                required
              />
            </v-col>

            <v-col
              cols="12"
              md="4"
            >
              <v-text-field
                v-model="vehicles.brand_office"
                label="Sucursal"
                outlined
                prepend-icon="mdi-map-marker"
                required
              />
            </v-col>

            <v-col
              cols="12"
              md="4"
            >
              <v-text-field
                v-model="vehicles.price"
                label="Precio"
                outlined
                prepend-icon="mdi-cash"
                prefix="$"
                required
              />
            </v-col>
          </v-row>

          <v-row>
            <v-col
              cols="12"
              md="12"
            >
              <v-select
                v-model="vehicles.status"
                label="Estado"
                :items="['Libre', 'Asignado', 'Por Conseguir','Exhibición']"
                outlined
                prepend-icon="mdi-group"
                required
              />
            </v-col>
          </v-row>

          <v-card-actions>
            <v-btn
              to="/adv/vehiculos"
              color="blue-grey"
              class="mr-4"
            >
              Cancelar
            </v-btn>
            <v-btn
              type="submit"
              color="green"
              class="mr-4"
            >
              Guardar
            </v-btn>
          </v-card-actions>
        </form>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
  import axios from 'axios'

  const url = 'https://sistema-adv.herokuapp.com/api/vehicles/'

  export default {
    name: 'CrearVehiculo',
    data () {
      return {
        marcas: ['Toyota', 'Hino'],
        categoria: ['Sedan', 'Hatchback', 'SUV', 'PickUp', 'Coupe'],
        vehicles: {
          brand: '',
          cod_tdp: '',
          serie_tdp: '',
          category: '',
          model: '',
          colour: '',
          manufacturing_year: '',
          model_year: '',
          brand_office: '',
          location: '',
          price: '',
          status: '',
        },
        opciones: [
          'Falta Asignación',
          'En tránsito',
          'Aduanas',
          'Nacionalización',
          'En Cigüeña',
          'Jaen',
          'Tarapoto',
          'Entregado',
        ],
      }
    },
    mounted () {},
    methods: {
      guardarVehiculo () {
        const router = this.$router
        const params = this.vehicles

        axios.post(url, params).then((res) => {
          // console.log(res);
          router.push('/adv/vehiculos')
          this.limpiasCampos()
        })
      },
      limpiarCampos () {
        this.vehicles = ''
      },
    },
  }
</script>
